export default {
  name: 'AllPersonalities',
  data() {
    return {
      selectedObject: {
        color: '#eee',
      },
      barScale: 15,
      counts: {
        adventurers: 2,
        advocates: 8,
        campaigners: 2,
        commanders: 2,
        consuls: 1,
        debaters: 2,
        defenders: 3,
        logicians: 4,
        logisticians: 2,
        mediators: 4,
        protagonists: 4,
      },
     show: {
       adventurers: false,
       advocates: false,
       campaigners: false,
       commanders: false,
       consuls: false,
       debaters: false,
       defenders: false,
       logicians: false,
       logisticians: false,
       mediators: false,
       protagonists: false,
     }
    }
  },
  computed: {
    erectBar() {
      if (this.show.adventurers) {
        return {
          backgroundColor: '#1f81ac',
          width: this.$refs.window.clientWidth/this.barScale * this.counts.adventurers + 'px',
        }
      }
      if (this.show.advocates) {
        return {
          backgroundColor: '#1f81ac',
          width: this.$refs.window.clientWidth/this.barScale * this.counts.advocates + 'px',
        }
      }
      return {}
    },
    erectIcon() {
      if (this.show.adventurers) {
        return {
          marginLeft: this.$refs.window.clientWidth/this.barScale * this.counts.adventurers + 'px',
        }
      }
      if (this.show.advocates) {
        return {
          marginLeft: this.$refs.window.clientWidth/this.barScale * this.counts.advocates + 'px'
        }
      }
      return {}
    },
  }
}
